
import IEcharts from 'vue-echarts-v3/src/full.vue'
import ScoreItem from '@/components/ScoreItem'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
  components: {
    IEcharts,
    ScoreItem,
    XButton
  },
  data () {
    return {
      loading: true,
      bar: {
        tooltip: {},
        series: [{
          name: '',
          type: 'pie',
          radius: '70%',
          center: ['50%', '50%'],
          data: [
            {value: 335, name: ''},
            {value: 210, name: ''}
          ],
          itemStyle: {
            normal: {
              label: {
                show: false
              },
              labelLine: {
                show: false
              },
              borderWidth: 2,
              borderColor: 'rgba(255, 255, 255, 0.6)'
            }
          }
        }],
        color: ['#FDC800', '#43B5E4']
      }
    }
  },
  methods: {
    onReady (instance) {
      this.loading = !this.loading
    },
    jump (url) {
      this.$router.push(url)
    }
  }
}



