
import {mapGetters} from 'vuex'
import XImg from 'vux/src/components/x-img/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
  components: {
    XImg,
    Scroller
  },
  mounted () {
    this.height = document.querySelector('.content').clientHeight + 'px'
  },
  data () {
    return {
      height: '0px',
      status: {
        pullupStatus: 'default'
      },
      pulldown: {
        content: 'Pull Down To Refresh',
        height: 60,
        autoRefresh: true,
        downContent: '',
        upContent: '',
        loadingContent: '',
        clsPrefix: 'xs-plugin-pulldown-'
      },
      pullup: {
        content: 'Pull Up To Refresh',
        pullUpHeight: 60,
        height: 60,
        autoRefresh: true,
        downContent: '',
        upContent: '',
        loadingContent: '',
        clsPrefix: 'xs-plugin-pullup-'
      }
    }
  },
  computed: {
    ...mapGetters({
      list: 'getOfferList'
    })
  }
}



