import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      redirect: '/offer',
      component: require('@/views/TabView'),
      children: [
        {
          path: '/offer',
          components: {
            tab: require('@/views/Offer')
          }
        }, {
          path: '/mall',
          components: {
            tab: require('@/views/Mall')
          }
        }, {
          path: '/personer',
          components: {
            tab: require('@/views/Personer')
          }
        }
      ]
    }, {
      path: '/offer/:id',
      components: {
        default: require('@/views/OfferDetail')
      }
    }, {
      path: '/offer/:id/photograph',
      components: {
        default: require('@/views/PhotoGraph')
      }
    }, {
      path: '/login',
      name: 'Login',
      component: require('@/views/Login')
    }, {
      path: '/setting',
      name: 'Setting',
      component: require('@/views/Setting')
    }, {
      path: '/exchange',
      name: 'exchange',
      component: require('@/views/Exchange')
    }, {
      path: '/order',
      name: 'order',
      component: require('@/views/Order')
    }, {
      path: '/order/:id',
      name: 'orders',
      component: require('@/views/Order')
    }, {
      path: '/wallet',
      name: 'wallet',
      component: require('@/views/Wallet')
    }, {
      path: '/record',
      name: 'record',
      component: require('@/views/Record')
    }, {
      path: '/record/:id',
      name: 'recordDetail',
      component: require('@/views/RecordDetail')
    }, {
      path: '/cash',
      name: 'cash',
      component: require('@/views/Cash')
    }, {
      path: '/donation',
      name: 'donation',
      component: require('@/views/Donation')
    }, {
      path: '/detail',
      name: 'detail',
      component: require('@/views/Detail')
    }, {
      path: '/balanceDetail',
      name: 'balanceDetail',
      component: require('@/views/BalanceDetail')
    }, {
      path: '/message',
      name: 'message',
      component: require('@/views/Message')
    }, {
      path: '/information',
      name: 'information',
      component: require('@/views/Information')
    }, {
      path: '/edit/passwordBypay',
      name: 'passwordBypay',
      component: require('@/views/PasswordPay')
    }, {
      path: '/edit/passwordBylogin',
      name: 'passwordBylogin',
      component: require('@/views/PasswordLogin')
    }, {
      path: '/aboutUs',
      name: 'aboutUs',
      component: require('@/views/AboutUs')
    }, {
      path: '/feedback',
      name: 'feedback',
      component: require('@/views/FeedBack')
    }, {
      path: '/question',
      name: 'question',
      component: require('@/views/Question')
    }
  ]
})
router.beforeEach(function (to, from, next) {
  store.commit('updateLoadingStatus', true)
  console.log('')
  next()
})

router.afterEach(function (to) {
  store.commit('updateLoadingStatus', false)
  console.log('')
})
export default router



